
import Datetime from 'vux/src/components/datetime/index.vue'
import DatetimePlugin from 'vux/src/plugins/datetime/index.js'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import Vue from "vue";
import { mapState } from "vuex";
import { JDGetReportedDetails } from '@/api/getData'
Vue.use(DatetimePlugin);
import moment from 'moment'

export default {
data() {
return {
value5: [],
showPopupPicker: false,
data: [['按工序汇总产量', '产量原单明细']],
form: {
FStartTime: moment(new Date()).format('YYYY-MM-DD'),
FEndTime: moment(new Date()).format('YYYY-MM-DD'),
FWeixinOpenID :'oNESquBQPpQM-Ho3RF5U3ENdq6bU'
},
show: false,
startDate: "",
endDate: "",
visibility: false,
visibility2: false,
widths: 100,
tableHead: ["编号", "序号", "名字", "数量"],
orderList: []
};
},
computed: {
...mapState({
wxOpenId: state => state.mutations.wxOpenId,
userInfo: state => state.mutations.userInfo
})
},
components: {
PopupPicker,
Datetime
},
mounted() {
},
methods: {

search() {
this.$store.commit("updateLoadingStatus", { isLoading: true });
try {
this.form.FWeixinOpenID=this.wxOpenId;
JDGetReportedDetails(this.form).then(res => {
console.log(res);

this.$store.commit("SAVE_ORDERLIST", res);
this.$nextTick(() => {
this.$emit("init");
});
});
this.$emit("close");
} catch (error) {
} finally {
this.$emit("close");
this.$store.commit("updateLoadingStatus", { isLoading: false });
}
},

showPlugin() {
let _this = this;
this.$vux.datetime.show({
cancelText: "取消",
confirmText: "确定",
format: "YYYY-MM-DD",
value: _this.form.FStartTime,
onConfirm(val) {
console.log("plugin confirm", val);
_this.form.FStartTime = val;
},
onShow() {
console.log("plugin show");
},
onHide() {
console.log("plugin hide");
},
change(value){
_this.form.FStartTime = value;
console.log('change', value)
}
});
},

showPlugin2() {
let _this = this;
this.$vux.datetime.show({
cancelText: "取消",
confirmText: "确定",
format: "YYYY-MM-DD",
value: _this.form.FEndTime,
onConfirm(val) {
_this.form.FEndTime = val;
console.log("plugin confirm", val);
},
onShow() {
console.log("plugin show");
},
onHide() {
console.log("plugin hide");
},
change(value){
_this.form.FEndTime = value;
console.log('change', value)
}
});
},
onConfirm() {},
log() {},
popup() {
alert("0");
},
change () {

},
}
};



